import React, { useState, useEffect, useMemo } from 'react'
import { Button, Icon } from "react-materialize"
import { useTranslation } from "react-i18next"
import { useCookies } from "react-cookie"
import cx from 'classnames'

const WishlistButton = ({ item, isSubcategory }) => {

    const [cookies, setCookie, removeCookie] = useCookies(['wishlist'])
    const wSet = useMemo(() => new Set(Object.values(cookies.wishlist || [])), [cookies.wishlist])
    const [isFavorite, setIsFavorite] = useState(false)

    useEffect(() => {
        if (wSet.has(item.id)) setIsFavorite(!isFavorite)
    }, [])

    const { t } = useTranslation()

    const deleteWishlistItem = (item) => {
        if (!window.confirm(t("Вы уверены, что хотите убрать данный товар из списка желаний?"))) return
        const { [item]: deleted, ...otherProducts } = cookies.wishlist

        Object.keys(otherProducts).length ? setCookie('wishlist', otherProducts) : removeCookie('wishlist')
    }

    return <Button style={{ bottom: 5, right: 5, }}
        className={cx('notranslate', isSubcategory ?
            "green hoverable halfway-fab waves-effect waves-light" :
            "green hoverable waves-effect waves-light")}
        floating
        icon={<Icon>{isFavorite ? "favorite" : "favorite_border"}</Icon>}
        node="button"
        tooltip={t(!isFavorite ? "Добавить в избранное" : "Убрать из избранного")}
        tooltipOptions={{
            position: 'bottom'
        }}
        onClick={() => {
            if (!isFavorite) {
                setIsFavorite(!isFavorite)
                setCookie('wishlist', { ...(cookies.wishlist || {}), [item.id]: item.id })
            }
            else {
                deleteWishlistItem(item.id)
                setIsFavorite(!isFavorite)
            }
        }}
    />
}

export default WishlistButton
